#hours-div {
  background-size: cover !important; 
  background: url("bottom-curve.png"); 
  background-color: #BDBDBD; 
  height: 75vh; 
}

section {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 135px;
}

/* Card stuff */ 
.card-row-here {
  display: flex; 
  align-content: center !important; 
  justify-content: space-evenly; 
} 

.the-card-good {
  display: flex; 
  justify-content: center !important; 
  margin-left: auto; 
  margin-right: auto;  
  width: auto; 
  height: 35vh; 
  margin-top: 5vh !important; 
  margin-bottom: 5vh !important;
}

.the-good-card {
  display: inline-block; 
  margin-left: auto; 
  margin-right: auto; 
  width: auto; 
  height: 45vh; 
} 

.the-good-card-pc {
  display: inline-block; 
  margin-left: auto; 
  margin-right: auto; 
  width: auto; 
  height: 45vh; 
} 

.the-good-card-mobile {
  display: none; 
} 

.our-favorite-card {
  display: none; 
}

/* ------------------------------- */

#house {
  float: left; 
  margin-right: 10vw; 
  margin-top: 30.5px; 
  width: auto; 
  height: 30vh; 
  border-radius: 20px;  
  border: solid; 
  border-color: #1c2e6a; 
} 

#visit {
  max-width: 50vw !important; 
  margin-top: auto;
  margin-bottom: auto; 
}

/* ------V----BOTTOM ROW----V------ */

#artist {
  float: right;
  margin-left: 8vw; 
  margin-top: 37.5px; 
  width: auto; 
  height: 30vh;
} 

#explore {
  max-width: 50vw !important; 
  margin-top: 8vh; 
}

/* ------------------------------- */

#image-thing-home {
  display: flex; 
  justify-content: center !important;  
  margin-left: auto; 
  margin-right: auto; 
  margin-top: 45px; 
  margin-bottom: auto; 
  vertical-align: middle !important;
  width: auto; 
  height: 35vh; 
} 

#image-thing-home-1 {
  display: flex; 
  justify-content: center !important;  
  margin-left: 5vw !important; 
  margin-right: auto; 
  margin-top: 45px; 
  margin-bottom: auto; 
  vertical-align: middle !important;
  width: auto; 
  height: 35vh; 
} 

#image-thing-home-2 {
  display: flex; 
  justify-content: center;  
  margin-left: auto; 
  margin-right: auto; 
  margin-top: 50px; 
  vertical-align: middle;
  max-width: 46vw !important; 
  height: auto; 
} 

/*End Card Stuff*/ 

